// @flow
import React, { Fragment, useEffect } from 'react';
import type { Element } from 'react';
import List from 'components/SellerOfferList';
import { createStructuredSelector } from 'reselect';
import { connect } from 'react-redux';
import _get from 'lodash/get';
import { fetchOffers, saveOfferStep } from '../redux/Actions';
import { getAllOffers as getBuyerOffers } from 'containers/BuyerOffer/redux/Actions';
import { OFFER_STEPS } from 'shared/Consts';
import { withErrorBoundary } from 'components/ErrorBoundary';
import { toastr } from 'react-redux-toastr';
import { OFFER_ERROR_MESSAGES } from 'shared/Messages';
import ContingencyList from 'components/ContingencyList/Loadable';

type PropsWithoutRedux = {};

type Props = {
  fetchOffers: Function,
  offers: Array<Object>,
  isLoading: boolean,
  saveOfferStep: Function,
  isSubmitting: boolean,
  isUpdatedStep: boolean,
  error: Object,
  getBuyerOffers: Function,
};

const OfferList = (props: Props): Element<typeof Fragment> => {
  const {
    fetchOffers,
    offers,
    isLoading,
    saveOfferStep,
    isSubmitting,
    isUpdatedStep,
    error,
    getBuyerOffers,
  } = props;

  useEffect(() => {
    fetchOffers();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    if (isUpdatedStep) {
      fetchOffers();
      getBuyerOffers();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isUpdatedStep]);

  useEffect(() => {
    const { message } = error || {};

    if (message) {
      toastr.error(OFFER_ERROR_MESSAGES.saveFailure, message);
    }
  }, [error]);

  const handleApproval = async offerId => {
    saveOfferStep(offerId, { new_step: OFFER_STEPS.paBeingCreated });
  };

  const handleDecline = offerId => {
    saveOfferStep(offerId, { new_step: OFFER_STEPS.sellerDecline });
  };

  return (
    <>
      <List
        offers={offers}
        isLoading={isLoading}
        handleApproval={handleApproval}
        isSubmitting={isSubmitting}
        handleDecline={handleDecline}
      />
      <ContingencyList offers={offers} isLoading={isLoading} />
    </>
  );
};

const structuredSelector = createStructuredSelector({
  offers: state => _get(state, ['sellerOffers', 'list', 0, 'Offers']),
  isLoading: state => _get(state, ['sellerOffers', 'isLoading']),
  isSubmitting: state => _get(state, ['sellerOffer', 'isLoading']),
  isUpdatedStep: state => _get(state, ['sellerOffer', 'isUpdatedStep']),
  error: state => _get(state, ['sellerOffer', 'error']),
});

const mapDispatchToProps = {
  fetchOffers,
  saveOfferStep,
  getBuyerOffers,
};

export default (connect(
  structuredSelector,
  mapDispatchToProps,
)(withErrorBoundary(OfferList)): Element<PropsWithoutRedux>);
